@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500;700;900&display=swap);
.App {
  text-align: center;
}
*{
  font-family: 'Roboto', sans-serif;
  font-size:medium;
  list-style: none;
}
body{
  margin: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navlink{
    color:rgb(3, 3, 3);
    text-decoration: none;
    background-color: none;
}
*{
    box-sizing: border-box;
}
.header{
    margin: 0;
}
.header__desktopVersion{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    align-items: center;
}
li{
    margin: 1rem;
}
ul{
    display: flex;
    justify-content: center;
    align-items: center;
}
a{
    display: block;
    position: relative;
    text-decoration: none;
    color: #000000;
    padding: 0.5em 0;

}
.anchorHeader::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.1em;
    background-color: rgb(0, 0, 0);
    opacity: 0;
    transition: opacity 300ms, -webkit-transform 300ms;
    transition: opacity 300ms, transform 300ms;
    transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
  }
.anchorHeader:hover,
.anchorHeader::after {
  opacity: 1;
  color: green;
}

li .anchorHeader {
    overflow: hidden;
  }
  
li .anchorHeader::after {
    opacity: 1;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
  
li .anchorHeader:hover::after,
li .anchorHeadera:focus::after{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
a{
    text-decoration:none
}
.correo:hover{
    color: green;
    text-decoration: underline;
}
.Main__Conectemos{
    height: 56.8vh;
}
.socialMedia__container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
}

.socialMedia__container > a:nth-child(1){
    margin-right: 2rem;
}

.footer{
    padding: 2rem;
}
.socialMedia__Footercontainer{
    display: flex;
    justify-content: center;
    align-content: center;
}
.textoAutor__footerContainer{
    color: #585858;
}
.socialMedia__Footercontainer > a:nth-child(1){
    margin-right: 2rem;
}
